@use 'custom' as c;
@use 'base/mixins' as m;
@use 'base/animaciones' as *;

.header {
    @include m.background-image('../img/header.jpg');
    position: relative;

    .header-guitarra {
        display: none;

        @include c.media-breakpoint-up(lg) {
            display: block;
            position: absolute;
            right: 0;
            bottom: 0;

            animation-duration: 1s;
            animation-delay: 5s;
            animation-timing-function: ease-in-out;
            animation-name: animarImagen;
        }
    }
}

.navegacion {
    position: relative;
    z-index: 1;

    .enlace {
        display: block;
        padding: 1rem;
        font-size: 1.8rem;
        font-weight: 700;

        &:hover {
            background-color: c.$primary;
        }
    }

    .carrito {
        filter: invert(1);
        width: 3rem; 
    }
}

.guitarra-imagen {
    width: 50%;
    margin: 0 auto;

    @include c.media-breakpoint-up(md) {
        width: 33%;
        margin: auto;
    }
}