.fw-black {
    font-weight: 900;
}

#registros {
    vertical-align: middle;
    text-align: center;
}

.borrar-info {
    all: unset;
    min-width: 5rem;
}

.min-w-10 {
    min-width: 10rem;
} 

.imprimir-mostrar {
    display: none;
}

@media print {
    @page {
        size: A4;
        margin: 1cm;
        orientation: portrait;
    }

    header,
    footer,
    .imprimir-ocultar {
        display: none;
    }

    .imprimir-mostrar {
        display: block;
    }
}

.modal-open {
    background-color: rgba(0 0 0 / 0.5) !important;
}